
'use client';
import { useState, useEffect, useMemo } from 'react';
import { DayPicker } from 'react-day-picker';
import 'react-day-picker/dist/style.css';
import { Header } from '@/components/layout/Header';
import { useAuth } from '@/hooks/use-auth';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { useToast } from '@/hooks/use-toast';
import { cn } from '@/lib/utils';
import { format, isSameDay } from 'date-fns';
import { Button } from '@/components/ui/button';
import { X, Loader2 } from 'lucide-react';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import type { Family, Volunteer, VolunteerAvailability } from '@/types';
import AppShell from '@/components/layout/AppShell';
import { useRouter } from 'next/navigation';
import { useFirebase, useMemoFirebase } from '@/firebase';
import { doc, setDoc } from 'firebase/firestore';
import { useDoc } from '@/firebase/hooks/use-doc';
import { FirestorePermissionError } from '@/firebase/errors';
import { errorEmitter } from '@/firebase/error-emitter';

export default function MyAvailabilityPage() {
  const { churchId, userId, isLoggedIn, isAdmin, user } = useAuth();
  const { firestore } = useFirebase();
  const { toast } = useToast();
  const router = useRouter();
  
  const [selectedMemberId, setSelectedMemberId] = useState<string>('');
  
  const familyDocRef = useMemoFirebase(() =>
    firestore && churchId && user?.familyId
      ? doc(firestore, 'churches', churchId, 'families', user.familyId)
      : null,
    [firestore, churchId, user]
  );
  const { data: family } = useDoc<Family>(familyDocRef);

  const availabilityDocRef = useMemoFirebase(() => {
    if (!firestore || !churchId || !selectedMemberId || selectedMemberId === 'all') return null;
    return doc(firestore, 'churches', churchId, 'volunteerAvailability', selectedMemberId);
  }, [firestore, churchId, selectedMemberId]);
  
  const { data: availability, isLoading } = useDoc<VolunteerAvailability>(availabilityDocRef);
  const unavailableDays = useMemo(() => (availability?.unavailableDates || []).map(d => d instanceof Date ? d : d.toDate()), [availability]);

  useEffect(() => {
    if (!isLoggedIn) {
      router.push('/');
    } else if (isAdmin) {
       router.push('/admin');
    } else if (userId) {
        setSelectedMemberId(userId);
    }
  }, [isLoggedIn, isAdmin, router, userId]);

  const toggleAvailability = (day: Date) => {
    if (!firestore || !churchId || !selectedMemberId || selectedMemberId === 'all') {
      if (selectedMemberId === 'all') {
        toast({
          variant: 'destructive',
          title: 'Cannot Modify All',
          description: 'Please select an individual family member to modify availability.',
        });
      }
      return;
    }
    
    if (!availabilityDocRef) return;

    const isCurrentlyUnavailable = unavailableDays.some(d => isSameDay(d, day));
    const newUnavailableDates = isCurrentlyUnavailable
      ? unavailableDays.filter(d => !isSameDay(d, day))
      : [...unavailableDays, day];
      
    const availabilityData = { unavailableDates: newUnavailableDates, volunteerId: selectedMemberId, churchId };

    setDoc(availabilityDocRef, availabilityData, { merge: true })
        .then(() => {
            toast({
                title: "Availability Updated",
                description: `You marked ${format(day, 'PPP')} as ${!isCurrentlyUnavailable ? 'unavailable' : 'available'}.`
            });
        })
        .catch(async (serverError) => {
            const permissionError = new FirestorePermissionError(user, {
                path: availabilityDocRef.path,
                operation: 'update',
                requestResourceData: availabilityData,
            });
            errorEmitter.emit('permission-error', permissionError);
        });
  }
  
  if (!isLoggedIn || isAdmin || !firestore) {
    return null;
  }

  const modifiers = {
    unavailable: unavailableDays
  }

  const modifiersStyles = {
    unavailable: { 
        backgroundColor: 'hsl(var(--destructive))',
        color: 'hsl(var(--destructive-foreground))'
    }
  }

  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex-1 p-4 md:p-8">
          <div className="max-w-4xl mx-auto space-y-6">
            <div className="flex justify-between items-start">
              <div>
                  <h1 className="text-3xl font-bold font-headline tracking-tight">
                  My Availability
                  </h1>
                  <p className="text-muted-foreground mt-1">
                  Select dates on the calendar to mark them as unavailable.
                  </p>
              </div>
              {family && user?.preferences.familyManagementEnabled && (
                  <Select value={selectedMemberId} onValueChange={setSelectedMemberId}>
                      <SelectTrigger className="w-[280px]">
                          <SelectValue placeholder="Select a family member" />
                      </SelectTrigger>
                      <SelectContent>
                          <SelectItem value="all">All Family Members</SelectItem>
                          {family.members.map(member => (
                              <SelectItem key={member.id} value={member.id}>{member.name} {member.id === userId && '(Me)'}</SelectItem>
                          ))}
                      </SelectContent>
                  </Select>
              )}
            </div>
            
            <div className="grid gap-6 md:grid-cols-2">
              <Card>
                  <CardContent className="p-2 md:p-4 flex justify-center">
                  <style>{`.rdp-day_selected { background-color: hsl(var(--destructive)); color: hsl(var(--destructive-foreground)); } .rdp-day_selected:hover { background-color: hsl(var(--destructive)); opacity: 0.9; } .rdp-button:hover:not([disabled]):not(.rdp-day_selected) { background-color: hsl(var(--accent)); }`}</style>
                  {isLoading ? (
                      <div className="flex items-center justify-center h-full min-h-[300px]">
                          <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
                      </div>
                  ): (
                  <DayPicker
                      mode="multiple"
                      min={0}
                      selected={unavailableDays}
                      onDayClick={(day) => toggleAvailability(day)}
                      modifiers={modifiers}
                      modifiersStyles={modifiersStyles}
                      className="w-full"
                      classNames={{
                          months: 'flex flex-col sm:flex-row space-y-4 sm:space-x-4 sm:space-y-0',
                          month: 'space-y-4 w-full',
                          table: 'w-full border-collapse',
                          head_row: 'flex justify-around',
                          head_cell: 'w-full text-muted-foreground font-normal',
                          row: 'flex w-full mt-2 justify-around',
                          cell: 'w-full',
                          day: cn(
                          'h-12 w-12 p-0 font-normal rounded-full relative focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary',
                          ),
                          day_today: 'font-bold text-primary',
                          day_outside: 'text-muted-foreground opacity-50',
                      }}
                  />
                  )}
                  </CardContent>
              </Card>

              <Card>
                  <CardHeader>
                      <CardTitle>Unavailable Days</CardTitle>
                      <CardDescription>A list of dates marked as unavailable.</CardDescription>
                  </CardHeader>
                  <CardContent>
                      <ScrollArea className="h-72">
                          {isLoading ? (
                              <div className="flex items-center justify-center h-full min-h-[200px]">
                                  <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
                              </div>
                          ) : unavailableDays && unavailableDays.length > 0 ? (
                              <ul className="space-y-2 pr-4">
                                  {unavailableDays.sort((a,b) => a.getTime() - b.getTime()).map((day) => (
                                  <li key={day.toISOString()} className="flex items-center justify-between p-2 rounded-md bg-muted">
                                      <span className="font-medium">{format(day, 'EEEE, MMMM d, yyyy')}</span>
                                      <Button variant="ghost" size="sm" onClick={() => toggleAvailability(day)}>
                                          <X className="mr-2 h-4 w-4" />
                                          Mark as Available
                                      </Button>
                                  </li>
                                  ))}
                              </ul>
                          ) : (
                              <p className="text-sm text-muted-foreground text-center py-10">No upcoming unavailable days.</p>
                          )}
                      </ScrollArea>
                  </CardContent>
              </Card>
            </div>
          </div>
        </main>
      </div>
    </AppShell>
  );
}
